import { styled } from "@rp/react/theme"
import { TableCell, Typography } from "@rp/react/web/mui/core"

type FileStatusCellStyledProp = {
  shouldDisplayControls?: boolean
}
type FileNameCellStyledProp = {
  failed?: boolean
}
export const TableCellStyled = styled(TableCell, {
  name: "TableCellStyled",
})({
  padding: "12px 16px",
})
export const FileStatusCellStyled = styled(TableCell, {
  name: "FileStatusCellStyled",
  shouldForwardProp: prop => prop !== "shouldDisplayControls",
})<FileStatusCellStyledProp>(({ shouldDisplayControls }) => ({
  padding: shouldDisplayControls ? "7px 16px" : "12px 16px",
  width: 220,
}))
export const FileNameCellStyled = styled(TableCellStyled, {
  name: "FileNameCellStyled",
  shouldForwardProp: prop => prop !== "failed",
})<FileNameCellStyledProp>(({ failed }) => ({
  whiteSpace: "nowrap",
  overflow: "hidden",
  textOverflow: "ellipsis",
  maxWidth: 250,
  color: failed ? "text.disabled" : "inherit",
}))
export const FileTypeCellStyled = styled(TableCellStyled, {
  name: "FileTypeCellStyled",
})({
  width: 0, // It reduces the column into its min width due its content
  "& div": {
    height: 24,
  },
})
export const SuccessTypography = styled(Typography, { name: "SuccessTypography" })(({ theme }) => ({
  color: theme.palette.success.main,
  textTransform: "uppercase",
}))
export const HintedTypography = styled(Typography, { name: "HintedTypography" })({
  color: "text.disabled",
  textTransform: "uppercase",
})
